<template>
    <div class="datetime-picker">
    
        <div class="picker-wrap">
            <table class="date-picker">
                <thead>
                    <tr class="date-head">
                        <!-- <th colspan="4">
                            <span class="btn-prev" @click="yearClick(-1)"></span>
                            <span class="show-year">{{now.getFullYear()}}</span>
                            <span class="btn-next" @click="yearClick(1)"></span>
                        </th> -->
                        <th colspan="7">
                            <span class="btn-prev" @click="monthClick(-1)"></span>
                            <span class="show-month">{{months[now.getMonth()]}}</span>
                            <span class="btn-next" @click="monthClick(1)"></span>
                        </th>
                    </tr>
                    <!-- <tr class="date-days">
                        <th v-for="day in days" :key="day">{{day}}</th>
                    </tr> -->
                </thead>
                <tbody>
                    <tr 
                        v-for="(v, i) in 6" 
                        :key="i">
                        <td 
                            v-for="(v, j) in 7"
                            :key="j"
                            :class="date[i * 7 + j] && date[i * 7 + j].status"
                            @click="pickDate(i * 7 + j)">{{date[i * 7 + j] && date[i * 7 + j].text}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    
        <!-- <input
            type="text"
            v-bind="inputAttr"
            :name="name"
            :style="inputStyle"
            :class="inputClass"
            :readonly="readonly"
            :value="pickedValue"> -->
   
    </div>
</template>

<script>
    export default {
        props: {
            readonly: { type: Boolean, default: false },
            value: { type: String, default: '' },
            format: { type: String, default: 'DD.MM.YYYY' },
            name: { type: String, default: '' },
            inputAttr: Object,
            inputStyle: [Object, Array],
            inputClass: [Object, Array]
        },
        data () {
            return {
                show: true,
                days: ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'],
                months: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'],
                date: [],
                now: new Date(),
                pickedValue: ''
            }
        },
        watch: {
            now () {
                this.update()
            },
            show () {
                this.update()
            }
        },
        methods: {
            close () {
                this.show = false
            },
            update () {
                var arr = []
                var time = new Date(this.now)
                time.setMonth(time.getMonth(), 1) // the first day
                var curFirstDay = time.getDay()
                curFirstDay === 0 && (curFirstDay = 7)
                time.setDate(0) // the last day
                var lastDayCount = time.getDate()
                for (let i = curFirstDay; i > 0; i--) {
                    arr.push({
                        text: lastDayCount - i + 1,
                        time: new Date(time.getFullYear(), time.getMonth(), lastDayCount - i + 1),
                        status: 'date-pass'
                    })
                }

                time.setMonth(time.getMonth() + 2, 0) // the last day of this month
                var curDayCount = time.getDate()
                time.setDate(1) // fix bug when month change
                var value = this.pickedValue || this.stringify(new Date())
                for (let i = 0; i < curDayCount; i++) {
                    let tmpTime = new Date(time.getFullYear(), time.getMonth(), i + 1)
                    let status = ''
                    this.stringify(tmpTime) === value && (status = 'date-active')
                    arr.push({
                        text: i + 1,
                        time: tmpTime,
                        status: status
                    })
                }

                var j = 1
                while (arr.length < 42) {
                    arr.push({
                        text: j,
                        time: new Date(time.getFullYear(), time.getMonth() + 1, j),
                        status: 'date-future'
                    })
                    j++
                }
                this.date = arr
            },
            yearClick (flag) {
                this.now.setFullYear(this.now.getFullYear() + flag)
                this.now = new Date(this.now)
            },
            monthClick (flag) {
                this.now.setMonth(this.now.getMonth() + flag, 1)
                this.now = new Date(this.now)
            },
            pickDate (index) {
                this.show = false
                this.now = new Date(this.date[index].time)
                this.pickedValue = this.stringify()
                this.$emit('pickDate', this.pickedValue)
            },
            parse (str) {
                var time = new Date(str)
                return isNaN(time.getTime()) ? null : time
            },
            stringify (time = this.now, format = this.format) {
                var year = time.getFullYear()
                var month = time.getMonth() + 1
                var date = time.getDate()
                var monthName = this.months[time.getMonth()]

                var map = {
                    YYYY: year,
                    MMM: monthName,
                    MM: ('0' + month).slice(-2),
                    M: month,
                    DD: ('0' + date).slice(-2),
                    D: date
                }
                return format.replace(/Y+|M+|D+/g, function (str) {
                    return map[str]
                })
            },
            leave (e) {
                if (!this.$el.contains(e.target)) {
                    this.close()
                }
            }
        },
        mounted () {
            this.pickedValue = this.value
            this.$nextTick(() => {
            this.now = this.parse(this.pickedValue) || new Date()
                document.addEventListener('click', this.leave, false)
            })
        },
        beforeDestroy () {
            document.removeEventListener('click', this.leave, false)
        }
    }
</script>

